<template>
  <div class="Persability">
    <div class="pub_title">
	    <span class="iconfont icon-nengliguanli1"></span>
	    <span>个人能力</span>
    </div>
    <div class="pub_info">
    	<div class="skillbarList" v-for="item in skillList">
        <div class="key">{{item.key}}</div>
        <div class="skillbar">
          <div class="skillbar-bar" :style="{width:item.percent*100+'%'}"></div>
          <!-- <div class="skill-bar-percent">{{item.percent*100}}%</div> -->
        </div>
        <!-- End Skill Bar -->
      </div>

    </div>
  </div>
</template>

<script>
export default {
  name: 'Persability',
  data () {
    return {
    	msg: '1',
    	skillList:[
    		{"key":"HTML","percent":0.9},
    		{"key":"CSS","percent":0.9},
    		{"key":"JS","percent":0.8},
    		{"key":"Vue","percent":0.8},
    		{"key":"Git","percent":0.75},
    		{"key":"mint-ui","percent":0.7},
    		{"key":"mui","percent":0.7},
    		{"key":"jQuery","percent":0.6},
    		{"key":"node","percent":0.2}
    	],
    }
  },
  created(){
  }
}
</script>

<style scoped>
	.skillbarList {
		position: relative;
		box-sizing: border-box;
	}

  .pub_info .skillbarList .key {
    width:46px;
  } 
	.skillbar {
    display: inline-block;
    width: 60%;
    height: 7px;
    background: #D3D4D9;
    border-radius: 3px;
	}
	.skillbar-bar {
		overflow: hidden;
    height: 7px;
    border-radius: 3px;
    /*width: 0px;*/
    width: 30%;
    background: rgb(232, 137, 136);
	}
	.skill-bar-percent {
		left: 70%;
    position: absolute;
    top: 0px;
    margin-left: -20px;
	}
</style>



