import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import PersInfo from '@/components/PersInfo'
import Education from '@/components/Education'
import Persability from '@/components/Persability'
import Experience from '@/components/Experience'
import Evaluation from '@/components/Evaluation'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/',name: 'Home',component: Home},
    {path: '/Home',name: 'Home',component: Home},
    {path: '/PersInfo',name: 'PersInfo',component: PersInfo},
    {path: '/Education',name: 'Education',component: Education},
    {path: '/Persability',name: 'Persability',component: Persability},
    {path: '/Experience',name: 'Experience',component: Experience},
    {path: '/Evaluation',name: 'Evaluation',component: Evaluation},
    
  ]
})



