<template>
  <div id="app">
    <div id="sidebar">
      <div class="photo">
        <img src="../static/images/fmain.jpg" alt="">
      </div>
      <div class="name_intent">
        <div class="name">方明鑫</div>
        <div class="intention">求职意向：前端实习生</div>
      </div>
      <!-- 使用基本信息组件 -->
      <div class="persInfo"><pers-info></pers-info></div>
      <!-- 使用个人能力组件 -->
      <div class="PersAbility"><pers-ability></pers-ability></div>
    </div>
    <div id="content">
      <!-- <router-view></router-view> -->
      <!-- 导入自我评价组件 -->
      <Evaluation></Evaluation>
      <!-- 添加装饰蒙版 -->
      <div class="stripLeft">
        
      </div>
      <div class="stripRight">
        
      </div>
      <div class="line"></div>
      <div class="ex_ed">
        <div class="edu">
          <Education></Education>
        </div>
        <div class="exp">
          <Experience></Experience>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import PersInfo from './components/PersInfo'
  import Education from './components/Education'
  import PersAbility from './components/PersAbility'
  import Experience from './components/Experience'
  import Evaluation from './components/Evaluation'

  export default {
    name: 'app',
    components: {
      PersInfo,
      Education,
      PersAbility,
      Experience,
      Evaluation,
    }
  }
</script>

<style>
  #app {
    width: 70%;
    height: 100%;
    margin-left: 15%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 2px #888888;
    text-align: center;
    font-family: "微软雅黑";
    background: #ECEDF1;
    min-width: 868px;
    overflow: hidden;
  }
  a {
    color: #7A7B7F;
    text-decoration: none;
  }
  
  /*设置侧边栏样式*/
  #sidebar {
    float: left;
    width: 34%;
    min-width: 210px;
    background: #ECEDF1;
    box-sizing: border-box;
    padding-top: 60px;
  }
  #sidebar .photo {
    display: inline-block;
    background: blue;
    width: 140px;
    height: 140px;
    border-radius: 100%;
    overflow: hidden;
  }
  #sidebar .photo img{
    width: 100%;
  }
  #sidebar .name_intent {
    box-sizing: border-box;
    margin-top: 32px;
    padding: 10px;
    height: 105px;
    background: linear-gradient(to right, #E0768A , #A2A4B9)
  }
  #sidebar .name_intent .name {
    font-size: 32px;
  }
  #sidebar .name_intent .intention {
    margin-top: 20px;
    font-size: 18px;
  }
  .persInfo,.PersAbility {
    width: 90%;
    margin-left: 5%;
  }
  /*设置小模块的公共样式*/
  .pub_title, .pub_info {
    font-weight: 600;
    text-align: left;
  }
  .pub_title {
    font-size: 20px;
    line-height: 86px;
    color: #DE7488;
    margin-top: 20px;
  }
  .pub_title span {
    margin-left: 6px;
  }
  .pub_title .iconfont {
    font-size: 26px;
  }
  .pub_info {
    font-size: 12px;
    line-height: 14px;
    color: #7A7B7F;
    padding-left: 18px;
  }
  .pub_info .key {
    width:64px;
    height:100%;
    display:inline-block;
    text-align: justify;
    vertical-align:top;
    /*background-color: #fff;*/
  } 
  .pub_info .key::after{
    content:"";
    display: inline-block;
    width:100%;
    overflow:hidden;
    height:0;
  }
  .pub_info span {
    margin-left: 8px;
  }
  /*设置主体内容样式*/
  #content {
    position: relative;
    float: right;
    width: 66%;
    /*background: #fff;*/
    /*min-width: 530px;*/
    padding: 30px 50px;
    background: #fff;
    box-sizing: border-box;
  }
  #content .pub_title {
    line-height: 46px;
    color: #6BB4D4;
    margin-top: 20px;
  }
  #content .pub_title .iconfont {
    background: #fff;
    position: relative;
    z-index: 2;
  }
  #content .pub_info {
    line-height: 32px;
    padding-left: 32px
  }
  #content .stripLeft {
    position: absolute;
    left: -2px;
    /*left: 0px;*/
    top: 235px;
    width: 48%;
    height: 105px;
    border-radius: 0 120px 60px 0;
    background: linear-gradient(to right, #A2A4B9 , #6DB5CD);
  }
  #content .stripRight {
    position: absolute;
    right: -2px;
    /*right: 0px;*/
    top: 235px;
    width: 48%;
    height: 105px;
    border-radius: 60px 0 0 120px;
    background: #E9EAEE;
  }
  #content .line {
    position: absolute;
    left: 66px;
    top: 410px;
    width: 2px;
    border: 1px solid #BCDDEB;
    height: 100%;
    background: #6BB4D4;
    z-index: 1;
  }
  #content .ex_ed {
    margin-top: 186px;
  }
  #content .ex_ed .pub_info h4 {
    color: #525254;
  }
  #content .ex_ed .pub_info a {
    color: #525254;
  }
  #content .ex_ed .pub_info li {
    line-height: 26px;
  }
</style>



